import { FC } from "react";
import styled from "styled-components";

interface Props {
  data: {
    title: string;
    description: string;
    img: string;
  };
}

const Img = styled.img`
  width: 100%;
  height: 100%;
  padding: 1rem;
`;
const Item = styled.div`
  flex: 1;
  display: flex;
  flex-direction: column;
  padding: 2rem;
  margin: 1rem 1rem 1rem 1rem;
  align-items: center;
  p {
    color: #777;
  }
`;
export const GridItem: FC<Props> = ({ data }) => {
  return (
    <Item>
      <Img src="design.png" />
      <h1>{data.title}</h1>
      <p>{data.description}</p>
    </Item>
  );
};
